import { CustomerIdentityIdOnExternalPlatform } from '@brandembassy/ts-types-platform/dist/endUser/EndUserIdentityId';

import { WebSocketClient } from '../sockets/WebSocketClient';

import { Customer } from './Customer';

export class CustomerInstance {
  private static instance: Customer;

  public static init(
    id?: CustomerIdentityIdOnExternalPlatform,
    name?: string,
    image?: string,
    websocketClient?: WebSocketClient | null,
  ): Customer {
    CustomerInstance.instance = new Customer(id, name, image, websocketClient);

    return CustomerInstance.instance;
  }

  public static getInstance(): Customer {
    if (!CustomerInstance.instance) {
      throw new Error('Init Customer first.');
    }

    return CustomerInstance.instance;
  }
}
