import { CALLER_SERVICE_ID } from '@brandembassy/ts-types-platform/dist/http/headers';

import { CHAT_SDK_VERSION } from '../env/version';
import { SDKVariableStorage } from '../variables/VariableStorage';

interface DefaultHeaders {
  Accept: string;
  [CALLER_SERVICE_ID]: string;
  'Content-Type': string;
  'x-sdk-platform': string;
  'x-sdk-version': string;
}

export const getDefaultHeaders = (): DefaultHeaders => ({
  Accept: 'application/json',
  'Content-Type': 'application/json',
  [CALLER_SERVICE_ID]: SDKVariableStorage.APP_NAME ?? '',
  'x-sdk-platform': 'web',
  'x-sdk-version': CHAT_SDK_VERSION,
});
